import React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Hero from "../components/index/hero"

import '../styles/index.css'
import Reasons from "../components/shared/reasons"
import VideoSection from "../components/index/video"
import MapSection from "../components/index/map"
import BottomCta from "../components/shared/bottom-cta"
import TestimonialSlider from "../components/shared/testimonial-slider"
import ImageTextSection from "../components/index/image-text"

import constants from '../constants/index'
import PhoneSection from "../components/index/phone-section"
import HorizontalSteps from "../components/index/horizontal-steps"

const IndexPage = ({ data }) => (
  <Layout>
    <SEO title="Home" />

    <Hero />
    <Reasons
      sectionTitle={<h2><span className='text-purple'>Четири причини</span><br />да се качиш на <br />NetSurf-а</h2>}
      reasonsData={constants.reasons}
      textClassName={'text-black'}
    />
    <VideoSection />
    <MapSection />

    <HorizontalSteps />

    <ImageTextSection
      imgSrc={data.forHomeImage.childImageSharp.fluid}
      imgAlt={'Телевизия и интернет за дома'}
      sectionTitle={<h2>Какво предлагаме<br /><span className='text-purple'> За дома</span></h2>}
      undertext={constants.forHomeSection.text}
      list={constants.forHomeSection.list}
      linkTo={constants.forHomeSection.linkTo}
      linkText={constants.forHomeSection.linkText}
      order={false}
    />

    <ImageTextSection
      imgSrc={data.forOfficeImage.childImageSharp.fluid}
      imgAlt={'Телевизия и интернет за офиса'}
      sectionTitle={<h2>Какво предлагаме<br /><span className='text-purple'> За офиса</span></h2>}
      undertext={constants.forOfficeSection.text}
      list={constants.forOfficeSection.list}
      linkTo={constants.forOfficeSection.linkTo}
      linkText={constants.forOfficeSection.linkText}
      order={true}
    />

    <PhoneSection />

    <TestimonialSlider 
      sectionTitle={<h2>Какво мислят<br /><span className='text-purple'>другите за нас</span></h2>}
      opinions={constants.clientOpinions}
      isWhiteArrows={false}
    />

    <BottomCta
      bgColor={'transparent'}>
      <h2 className='mb-md-5 mb-2 d-none d-md-block'>Нямаме търпение да ти покажем<br /><span className='text-purple'> колко сме добри!</span></h2>
      <h2 className='mb-md-5 mb-2 d-block d-md-none'>Нямаме търпение да ти покажем<span className='text-purple'> колко сме добри!</span></h2>
      <p className='mb-md-5 mb-3'>Нашите пакети – по-благи от тези под коледната елха</p>
      <Link to={'/za-doma'} className='btn btn-primary green'>Виж пакетите</Link>
    </BottomCta>
  </Layout>
)

export const query = graphql`
  query IndexPageQuery {
    forHomeImage: file(relativePath: {eq: "index/for-home.png"}) {
      childImageSharp {
        fluid (maxHeight: 700) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    forOfficeImage: file(relativePath: {eq: "index/for-office.png"}) {
      childImageSharp {
        fluid (maxHeight: 700) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

export default IndexPage
